

import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import classNames from "classnames/bind";
import styles from "./ResetPassword.module.scss";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const cx = classNames.bind(styles);

function ResetPassword() {
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const location = useLocation();
  const { email, phone } = location.state || {};
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMessage("");

    try {
      const res = await axios.post("http://localhost:5000/api/auth/reset-password", {
        phone,
        email,
        password,
        confirmPassword,
      });

      if (res.status === 200) {
        alert("Mt khu ca bn  c t li thnh cng!");
        navigate('/');
      }
    }
    catch(error) {
      if (error.response) {
        setErrorMessage(error.response.data.message || 'ng nhp tht bi');
      } else {
        setErrorMessage('Li kt ni server');
      }
    }
  };

  const toggleShowPassword = () => setShowPassword((prev) => !prev);
  const toggleShowConfirmPassword = () => setShowConfirmPassword((prev) => !prev);

  return (
    <div className={cx("container")}>
      <div className={cx("wrapper")}>
        <div className={cx("box")}>
          <h2 className={cx("title")}>t li mt khu</h2>
          <p className={cx("subtitle")}>
            Nhp mt khu mi v xc nhn  tip tc.
          </p>

          <form onSubmit={handleSubmit} className={cx("form")}>
            {/* Mt khu mi */}
            <div className={cx("form-group")}>
              <label htmlFor="password">Mt khu mi</label>
              <div className={cx("input-wrapper")}>
                <i className="fi fi-rr-lock" aria-hidden="true" />
                <input
                  id="password"
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="Nhp mt khu mi..."
                  className={cx("input")}
                  autoComplete="new-password"
                />
                <button
                  type="button"
                  className={cx("toggle-btn")}
                  onClick={toggleShowPassword}
                  tabIndex={-1}
                  aria-label={showPassword ? "n mt khu" : "Hin mt khu"}
                >
                  <i
                    className={showPassword ? "fi fi-rr-eye-crossed" : "fi fi-rr-eye"}
                    aria-hidden="true"
                  />
                </button>
              </div>
            </div>

            {/* Xc nhn mt khu */}
            <div className={cx("form-group")}>
              <label htmlFor="confirmPassword">Xc nhn mt khu</label>
              <div className={cx("input-wrapper")}>
                <i className="fi fi-rr-lock" aria-hidden="true" />
                <input
                  id="confirmPassword"
                  type={showConfirmPassword ? "text" : "password"}
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder="Nhp li mt khu..."
                  className={cx("input")}
                  autoComplete="new-password"
                />
                <button
                  type="button"
                  className={cx("toggle-btn")}
                  onClick={toggleShowConfirmPassword}
                  tabIndex={-1}
                  aria-label={showConfirmPassword ? "n mt khu" : "Hin mt khu"}
                >
                  <i
                    className={showConfirmPassword ? "fi fi-rr-eye-crossed" : "fi fi-rr-eye"}
                    aria-hidden="true"
                  />
                </button>
              </div>
            </div>

            {errorMessage && <p className={cx("error")}>{errorMessage}</p>}

            <button type="submit" className={cx("btn")}>
              Xc nhn
            </button>
          </form>

          <p className={cx("note")}>
            <a href="/">Quay li ng nhp</a>
          </p>
        </div>
      </div>
    </div>
  );
}

export default ResetPassword;



