import React from 'react';
import { UserPlus } from 'lucide-react';
import classNames from "classnames/bind";
import styles from "./Content2.module.scss";

const cx = classNames.bind(styles);

// Placeholder avatar
const avatarIcon = "data:image/svg+xml,%3csvg width='40' height='40' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='20' cy='20' r='20' fill='%23e5e7eb'/%3e%3ccircle cx='20' cy='16' r='6' fill='%239ca3af'/%3e%3cpath d='M20 22c-6 0-11 3-11 7v3h22v-3c0-4-5-7-11-7z' fill='%239ca3af'/%3e%3c/svg%3e";

const friendRequestsData = [
  { id: 1, name: "Hu t", avatar: avatarIcon, time: "2 ngy", phone: "0123456701", gender: "Nam", birthDate: "10/01/1998" },
  { id: 2, name: "Nguyn Vn An", avatar: avatarIcon, time: "1 ngy", phone: "0123456702", gender: "Nam", birthDate: "15/02/1999" },
  { id: 3, name: "Trn Th Bnh", avatar: avatarIcon, time: "3 gi", phone: "0123456703", gender: "N", birthDate: "20/03/2000" },
  { id: 4, name: "Phm Minh Chu", avatar: avatarIcon, time: "5 ngy", phone: "0123456704", gender: "N", birthDate: "25/04/2001" },
  { id: 5, name: "L Quc Dng", avatar: avatarIcon, time: "1 gi", phone: "0123456705", gender: "Nam", birthDate: "30/05/2002" },
  { id: 6, name: "V Th Hng", avatar: avatarIcon, time: "4 ngy", phone: "0123456706", gender: "N", birthDate: "05/06/2003" },
  { id: 7, name: "Hong Minh Khang", avatar: avatarIcon, time: "2 gi", phone: "0123456707", gender: "Nam", birthDate: "10/07/2004" },
  { id: 8, name: "Hong Minh Long", avatar: avatarIcon, time: "2 gi", phone: "0123456708", gender: "Nam", birthDate: "15/08/2005" }
];

const sentRequestsData = [
  { id: 1, name: "Xun Mai", avatar: avatarIcon, phone: "0123456711", gender: "N", birthDate: "01/01/1997" },
  { id: 2, name: " Duy", avatar: avatarIcon, initials: "DD", bgColor: "#ff6b35", phone: "0123456712", gender: "Nam", birthDate: "02/02/1998" },
  { id: 3, name: "Hong Thanh H", avatar: avatarIcon, phone: "0123456713", gender: "N", birthDate: "03/03/1999" },
  { id: 4, name: "L ng Hong An", avatar: avatarIcon, phone: "0123456714", gender: "Nam", birthDate: "04/04/2000" },
  { id: 5, name: "Vit H", avatar: avatarIcon, phone: "0123456715", gender: "N", birthDate: "05/05/2001" },
  { id: 6, name: "Nguyn Thnh Nam", avatar: avatarIcon, initials: "TN", bgColor: "#10b981", phone: "0123456716", gender: "Nam", birthDate: "06/06/2002" },
  { id: 7, name: "Trn Th Lan", avatar: avatarIcon, phone: "0123456717", gender: "N", birthDate: "07/07/2003" },
  { id: 8, name: "Phm Vn Hng", avatar: avatarIcon, phone: "0123456718", gender: "Nam", birthDate: "08/08/2004" }
];

function Content2({ onSelectFriend }) {
  const handleMenuClick = (friend) => {
    // Gi callback  bt u tr chuyn
    onSelectFriend(friend);
  };

  return (
    <div className={cx('content1')}>
      {/* Header */}
      <div className={cx('header')}>
        <div className={cx('header-container')}>
          <div className={cx('header-title')}>
            <UserPlus className={cx('header-icon')} />
            <span className={cx('title-text')}>Li mi kt bn</span>
          </div>
        </div>
      </div>

      <div className={cx('main-container')}>
        {/* Friend Requests Section */}
        <div className={cx('section')}>
          <div className={cx('section-header')}>
            <h2 className={cx('section-title')}>Li mi kt bn ({friendRequestsData.length})</h2>
          </div>

          {friendRequestsData.length === 0 ? (
            <div className={cx('empty-state')}>
              <div className={cx('empty-icon-wrapper')}>
                <svg className={cx('empty-icon')} fill="currentColor" viewBox="0 0 24 24">
                  <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                </svg>
              </div>
              <h3 className={cx('empty-text')}>Bn khng c li mi no</h3>
            </div>
          ) : (
            <div className={cx('cards-grid')}>
              {friendRequestsData.map(request => (
                <div key={request.id} className={cx('card')}>
                  <div className={cx('card-content')}>
                    <div className={cx('card-header')}>
                      <img
                        src={request.avatar}
                        alt={request.name}
                        className={cx('avatar')}
                      />
                      <div className={cx('user-info')}>
                        <h4 className={cx('user-name')}>{request.name}</h4>
                        <p className={cx('user-time')}>{request.time}</p>
                      </div>
                      <button
                        className={cx('card-menu-button')}
                        onClick={() => handleMenuClick(request)}
                      >
                        <svg className={cx('card-menu-icon')} fill="currentColor" viewBox="0 0 24 24">
                          <path d="M12 8c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z"/>
                        </svg>
                      </button>
                    </div>

                    <div className={cx('card-actions')}>
                      <button className={cx('btn-secondary')}>
                        Xa
                      </button>
                      <button className={cx('btn-primary')}>
                        Xc nhn
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Sent Requests Section */}
        <div className={cx('section')}>
          <div className={cx('section-header')}>
            <h2 className={cx('section-title')}>Li mi  gi ({sentRequestsData.length})</h2>
          </div>

          <div className={cx('cards-grid')}>
            {sentRequestsData.map(friend => (
              <div key={friend.id} className={cx('card')}>
                <div className={cx('card-content')}>
                  <div className={cx('card-header')}>
                    {friend.initials ? (
                      <div 
                        className={cx('avatar-initials')}
                        style={{ backgroundColor: friend.bgColor }}
                      >
                        {friend.initials}
                      </div>
                    ) : (
                      <img
                        src={friend.avatar}
                        alt={friend.name}
                        className={cx('avatar')}
                      />
                    )}
                    <div className={cx('user-info')}>
                      <h4 className={cx('user-name')}>{friend.name}</h4>
                    </div>
                    <button
                      className={cx('card-menu-button')}
                      onClick={() => handleMenuClick(friend)}
                    >
                      <svg className={cx('card-menu-icon')} fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 8c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z"/>
                      </svg>
                    </button>
                  </div>

                  <div className={cx('card-actions')}>
                    <button className={cx('btn-secondary')}>
                      Thu hi
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Content2;