
import React, { useState } from "react";
import classNames from "classnames/bind";
import { User, Upload, Calendar, Check } from "lucide-react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import styles from "./PersonalInfoForm.module.scss";

const cx = classNames.bind(styles);

function PersonalInfoForm() {
  const navigate = useNavigate();
  const location = useLocation();
  const username = location.state?.username;

  // State declarations - tch ring tng field
  const [fullname, setFullName] = useState("");
  const [gender, setGender] = useState("");
  const [birthday, setBirthDay] = useState("");
  const [avatar, setAvatar] = useState(null); // File object, not string
  const [imagePreview, setImagePreview] = useState(null);
  const [serverImage, setServerImage] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  // Handle image upload
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setAvatar(file);

      // Create preview
      const reader = new FileReader();
      reader.onload = (e) => {
        setImagePreview(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  // Form validation
  const isFormValid = fullname && gender && birthday;

  const handleSubmit = async (e) => {
    if (!isFormValid) return;

    setIsLoading(true);
    try {
      // Create FormData for file upload
      const submitData = new FormData();
      submitData.append('fullname', fullname);
      submitData.append('gender', gender);
      submitData.append('birthday', birthday);
      submitData.append('username', username);
      
      if (avatar) {
        submitData.append('avatar', avatar);
      }

      const response = await axios.post(
        "http://localhost:5000/api/auth/update-profile",
        submitData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        }
      );

      if (response.status === 200) {
        console.log("Cp nht thng tin thnh cng:", response.data);
        
        // Lu user v token vo localStorage
        if (response.data.user) {
          localStorage.setItem("user", JSON.stringify(response.data.user));
        }
        if (response.data.token) {
          localStorage.setItem("token", response.data.token);
        }
        
        // X l ng dn nh t server
        console.log("Profile image from server:", response.data.user?.profileImage || "None");
        if (response.data.user?.profileImage) {
          const imageUrl = `http://localhost:5000${response.data.user.profileImage}`;
          setServerImage(imageUrl);
          console.log("URL nh t server:", imageUrl);
        } else {
          console.log("Khng nhn c ng dn nh t server");
        }

        // Debug thng tin form
        console.log("H tn:", fullname);
        console.log("Gii tnh:", gender);
        console.log("Ngy sinh:", birthday);
        console.log("Username:", username); // Sa li cho ng


        setIsSubmitted(true);
        setErrorMessage("");
        setImagePreview(null);
        
        setTimeout(() => {
          setIsSubmitted(false);
          navigate("/dashboard", {
            state : { username: username }
          });
        }, 2000);
      }
    } catch (error) {
      console.error("Li khi cp nht thng tin:", error);
      setErrorMessage(
        error.response?.data?.message || "Li kt ni server! Vui lng th li."
      );
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className={cx("container")}>
      <div className={cx("wrapper")}>
        <div className={cx("box")}>
          <div className={cx("header")}>
            <h2 className={cx("title")}>Thng Tin C Nhn</h2>
            <p className={cx("subtitle")}>Vui lng in y  thng tin ca bn</p>
          </div>
          
          <form className={cx("form")} onSubmit={(e) => e.preventDefault()}>
            {/* Upload Avatar */}
            <div className={cx("avatarSection")}>
              <div className={cx("avatarWrapper")}>
                <div className={cx("avatarContainer")}>
                  {serverImage ? (
                    <img src={serverImage} alt="Profile" className={cx("avatarImage")} />
                  ) : imagePreview ? (
                    <img src={imagePreview} alt="Preview" className={cx("avatarImage")} />
                  ) : (
                    <img
                      src={"http://localhost:5000/uploads/default-avatar.png"}
                      alt="Default Avatar"
                      className={cx("avatarImage")}
                    />
                  )}
                </div>
                <label className={cx("uploadButton")}>
                  <Upload />
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleImageChange}
                    className={cx("hiddenInput")}
                    disabled={isLoading}
                  />
                </label>
              </div>
              <p className={cx("avatarLabel")}>Chn nh i din</p>
            </div>

            <div className={cx("formGrid")}>
              {/* Full Name */}
              <div className={cx("form-group", "fullWidth")}>
                <label className={cx("label")}>H v tn *</label>
                <div className={cx("input-wrapper")}>
                  <User className={cx("input-icon")} />
                  <input
                    type="text"
                    name="fullName"
                    value={fullname}
                    onChange={(e) => setFullName(e.target.value)}
                    placeholder="Nhp h v tn ca bn..."
                    className={cx("input")}
                    autoComplete="off"
                    required
                    disabled={isLoading}
                  />
                </div>
              </div>

              {/* Gender */}
              <div className={cx("form-group")}>
                <label className={cx("label")}>Gii tnh *</label>
                <div className={cx("genderOptions")}>
                  <label className={cx("genderOption")}>
                    <input
                      type="radio"
                      name="gender"
                      value="male"
                      checked={gender === "male"}
                      onChange={(e) => setGender(e.target.value)}
                      className={cx("hiddenInput")}
                      disabled={isLoading}
                    />
                    <div className={cx("genderButton", { selected: gender === "male" })}>
                      Nam
                    </div>
                  </label>
                  <label className={cx("genderOption")}>
                    <input
                      type="radio"
                      name="gender"
                      value="female"
                      checked={gender === "female"}
                      onChange={(e) => setGender(e.target.value)}
                      className={cx("hiddenInput")}
                      disabled={isLoading}
                    />
                    <div className={cx("genderButton", { selected: gender === "female" })}>
                      N
                    </div>
                  </label>
                </div>
              </div>

              {/* Birth Date */}
              <div className={cx("form-group")}>
                <label className={cx("label")}>Ngy sinh *</label>
                <div className={cx("input-wrapper")}>
                  <Calendar className={cx("input-icon")} />
                  <input
                    type="date"
                    name="birthDate"
                    value={birthday}
                    onChange={(e) => setBirthDay(e.target.value)}
                    className={cx("input")}
                    required
                    disabled={isLoading}
                  />
                </div>
              </div>
            </div>

            {/* Error Message */}
            {errorMessage && (
              <div className={cx("error-message")}>
                {errorMessage}
              </div>
            )}

            {/* Submit Button */}
            <button
              type="button"
              disabled={!isFormValid || isLoading}
              onClick={handleSubmit}
              className={cx("btn", { disabled: !isFormValid || isLoading })}
            >
              {isLoading ? (
                "ang x l..."
              ) : isSubmitted ? (
                <span className={cx("successMessage")}>
                  <Check />
                   lu thnh cng!
                </span>
              ) : (
                "Lu thng tin"
              )}
            </button>
          </form>

          {/* Form Data Preview */}
          {(fullname || gender || birthday || avatar) && (
            <div className={cx("preview")}>
              <h3 className={cx("previewTitle")}>Thng tin  nhp:</h3>
              <div className={cx("previewContent")}>
                {fullname && (
                  <p>
                    <span>H tn:</span> {fullname}
                  </p>
                )}
                {gender && (
                  <p>
                    <span>Gii tnh:</span> {gender === "male" ? "Nam" : "N"}
                  </p>
                )}
                {birthday && (
                  <p>
                    <span>Ngy sinh:</span> {new Date(birthday).toLocaleDateString("vi-VN")}
                  </p>
                )}
                <p>
                  <span>nh:</span>{" "}
                  {avatar ? avatar.name : "nh mc nh"}
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default PersonalInfoForm;