
import React, { useState, useCallback } from "react";
import classNames from "classnames/bind";
import styles from "./ChatInput.module.scss";
import EmojiPicker from "emoji-picker-react";
import { ThumbsUp, Send } from "lucide-react";

const cx = classNames.bind(styles);

function ChatInput({ onSend }) {
  const [text, setText] = useState("");
  const [open, setOpen] = useState(false);

  // Hm x l khi chn emoji t EmojiPicker
  const handleEmoji = useCallback((emojiObject) => {
    setText((prev) => prev + emojiObject.emoji);
    setOpen(false);
  }, []);

  const handleSend = useCallback(() => {
    if (text.trim()) {
      onSend(text.trim());
      setText("");
    } else {
      // Gi like nu khng c text
      onSend("");
    }
  }, [text, onSend]);

  const handleKeyPress = useCallback((e) => {
    if (e.key === "Enter") {
      handleSend();
    }
  }, [handleSend]);

  return (
    <>
      <div className={cx("input-area")}>
        <input
          type="text"
          placeholder="Nhp tin nhn..."
          value={text}
          onChange={(e) => setText(e.target.value)}
          onKeyPress={handleKeyPress}
          className={cx("message-input")}
          aria-label="Nhp tin nhn"
        />
        
        <div className={cx("emoji-container")}>
          <button
            className={cx("emoji-button")}
            onClick={() => setOpen((prev) => !prev)}
            aria-label="M bng chn biu tng cm xc"
          >
            <i class="fi fi-rr-smile"></i>
          </button>
          <div className={cx("emoji-picker")} style={{ display: open ? "block" : "none" }}>
            <EmojiPicker
              onEmojiClick={handleEmoji}
              width={300}
              height={400}
              theme="auto"
              emojiStyle="native"
              previewConfig={{ showPreview: false }}
            />
          </div>
        </div>
        
        <button
          onClick={handleSend}
          className={cx("send-button", { active: text.trim() })}
          aria-label={text.trim() ? "Gi tin nhn" : "Gi like"}
        >
          {text.trim() ? <Send size={20}/> : <ThumbsUp size={20}/>}
        </button>
      </div>

      {open && <div className={cx("overlay")} onClick={() => setOpen(false)} />}
    </>
  );
}

export default ChatInput;