
import classNames from "classnames/bind";
import styles from "./ChatHeader.module.scss";
import { Users, Search, Video, PanelRight, PanelLeft } from "lucide-react";
import { useState } from "react";

const cx = classNames.bind(styles);
const buckets = "ichigomazone";

function ChatHeader({ friend, onAvatarClick, onToggleDetail }) {
  const [pressed, setPressed] = useState(false);

  return (
    <div className={cx("top")}>
      <div className={cx("user-info")}>
        <button 
          className={cx("avatar-button")} 
          onClick={onAvatarClick} 
          title="Xem thng tin ngi dng" 
          aria-label="Xem thng tin ngi dng"
        >
          <div className={cx("avatar-container")}>
            <img 
              src={friend.avatar
                ? `https://${buckets}.s3.amazonaws.com/${friend.avatar}`
                : `https://${buckets}.s3.amazonaws.com/doraemon.png`
              }
            />
            <div className={cx("online-status")}></div>
          </div>
        </button>
        <div className={cx("user-details")}>
          <h2>{friend.name || "Ngi dng"}</h2>
          <span className={cx("status")}>ang hot ng</span>
        </div>
      </div>
      <div className={cx("actions")}>
        <button className={cx("action-btn")} title="Cuc gi thoi" aria-label="Cuc gi thoi">
          <Users size={16} />
        </button>
        <button className={cx("action-btn")} title="Video call" aria-label="Video call">
          <Search size={16} />
        </button>
        <button className={cx("action-btn")} title="Thng tin cuc tr chuyn" aria-label="Thng tin cuc tr chuyn">
          <Video size={16} />
        </button>
        <button className={cx("action-btn", "private", { active: pressed })} 
          aria-pressed={pressed}
          title="Ty chn khc" 
          onClick={(e) => {
            setPressed((prev) => !prev);
            onToggleDetail(e);
          }}
          aria-label="Ty chn khc">
            {pressed ? <PanelRight size={16}/> :< PanelLeft size={16}/>}
        </button>
      </div>
    </div>
  );
}

export default ChatHeader;