
import React, { useState } from "react";
import classNames from "classnames/bind";
import styles from "./ForgotPassword.module.scss";
import axios from "axios";
import { useNavigate } from 'react-router-dom';

const cx = classNames.bind(styles);

function ForgotPassword() {
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
  e.preventDefault();
  setErrorMessage('');
  
  try {
    const res = await axios.post("http://localhost:5000/api/auth/forgot-password", {
      email,
      phone,
    });
    
    if (res.status === 200) {
      navigate('/reset-password', {
        state : {
          email: email,
          phone: phone
        }
      });
    }
  } catch (error) {
    if (error.response) {
      setErrorMessage(error.response.data.message || 'ng nhp tht bi');
    } else {
      setErrorMessage('Li kt ni mng');
    }
  } 
};

  return (
    <div className={cx("container")}>
      <div className={cx("wrapper")}>
        <div className={cx("box")}>
          <h2 className={cx("title")}>Xc nhn ti khon</h2>
          <p className={cx("subtitle")}>
            Nhp email v s in thoi  ng k  xc nhn ti khon.
          </p>

          <form onSubmit={handleSubmit} className={cx("form")}>
            {/* Email */}
            <div className={cx("form-group")}>
              <label htmlFor="email">Email</label>
              <div className={cx("input-wrapper")}>
                <i className="fi fi-rr-envelope" aria-hidden="true" />
                <input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Nhp email ca bn..."
                  className={cx("input")}
                  autoComplete="off"
                />
              </div>
            </div>

            {/* S in thoi */}
            <div className={cx("form-group")}>
              <label htmlFor="phone">S in thoi</label>
              <div className={cx("input-wrapper")}>
                <i className="fi fi-rr-phone-call" aria-hidden="true" />
                <input
                  id="phone"
                  type="tel"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="Nhp s in thoi ca bn..."
                  className={cx("input")}
                  autoComplete="off"
                />
              </div>
            </div>

            <button type="submit" className={cx("btn")}>
              Gi yu cu xc nhn
            </button>
          </form>

          <p className={cx("note")}>
            <a href="/">Quay li ng nhp</a>
          </p>
        </div>
        {errorMessage && <p className={cx("error")}>{errorMessage}</p>}
      </div>
    </div>
  );
}

export default ForgotPassword;