
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import classNames from "classnames/bind";
import styles from "./Register.module.scss";
import axios from "axios";

const cx = classNames.bind(styles);

function Register() {
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMessage("");

    try{
      const res = await axios.post("http://localhost:5000/api/auth/register", {
        email,
        phone,
        password,
        confirmPassword,
      });

      if (res.status === 200) {
        alert("ng k thnh cng");
        navigate('/');
      }
    }
    catch(error) {
      if (error.response && error.response.data) {
        setErrorMessage(error.response.data.message);
      } 
      else {
        setErrorMessage("Li kt ni server!");
      }
    }
  };

  return (
    <div className={cx("container")}>
      <div className={cx("wrapper")}>
        <div className={cx("box")}>
          <h2 className={cx("title")}>ng k</h2>
          <p className={cx("subtitle")}>Nhp thng tin  to ti khon mi.</p>

          <form onSubmit={handleSubmit} className={cx("form")}>
            {/* Email */}
            <div className={cx("form-group")}>
              <label>Email</label>
              <div className={cx("input-wrapper")}>
                <i className="fi-rr-envelope"></i>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Nhp email ca bn..."
                  className={cx("input")}
                />
              </div>
            </div>

            {/* Phone */}
            <div className={cx("form-group")}>
              <label>S in thoi</label>
              <div className={cx("input-wrapper")}>
                <i className="fi-rr-phone-call"></i>
                <input
                  type="tel"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="Nhp s in thoi ca bn..."
                  className={cx("input")}
                />
              </div>
            </div>

            {/* Password */}
            <div className={cx("form-group")}>
              <label>Mt khu</label>
              <div className={cx("input-wrapper")}>
                <i className="fi-rr-lock"></i>
                <input
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="Nhp mt khu..."
                  className={cx("input")}
                />
                <button
                  type="button"
                  className={cx("toggle-btn")}
                  onClick={() => setShowPassword(!showPassword)}
                  tabIndex={-1}
                  aria-label={showPassword ? "n mt khu" : "Hin mt khu"}
                >
                  {showPassword ? (
                    <i className="fi fi-rr-eye-crossed" />
                  ) : (
                    <i className="fi fi-rr-eye" />
                  )}
                </button>
              </div>
            </div>

            {/* Confirm Password */}
            <div className={cx("form-group")}>
              <label>Xc nhn mt khu</label>
              <div className={cx("input-wrapper")}>
                <i className="fi-rr-lock"></i>
                <input
                  type={showConfirm ? "text" : "password"}
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder="Xc nhn mt khu..."
                  className={cx("input")}
                />
                <button
                  type="button"
                  className={cx("toggle-btn")}
                  onClick={() => setShowConfirm(!showConfirm)}
                  tabIndex={-1}
                  aria-label={showConfirm ? "n mt khu" : "Hin mt khu"}
                >
                  {showConfirm ? (
                    <i className="fi fi-rr-eye-crossed" />
                  ) : (
                    <i className="fi fi-rr-eye" />
                  )}
                </button>
              </div>
            </div>

            {/* Dng li c nh */}
            <p className={cx("error", { active: errorMessage })}>
              {errorMessage || "\u00A0"}
            </p>

            <button type="submit" className={cx("btn")}>
              ng k
            </button>
          </form>

          <p className={cx("note")}>
             c ti khon? <a href="/">ng nhp</a>
          </p>
        </div>
      </div>
    </div>
  );
}

export default Register;
